op: 30px;\n    right: 30px;\n    display: flex;\n}\n.venue-img-cont > div > button {\n    border: none;\n    outline: none;\n    width: 117px;\n    height: 31.1px;\n    background: #461952;\n    border-radius: 4.69859px;\n    font-family: \"Roboto\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 14.0958px;\n    line-height: 17px;\n\n    color: #ffffff;\n}\n.venue-img-cont > div > button:last-child {\n    color: #000000;\n    background-color: #ffffff;\n    margin-left: 10px;\n}\n.plate-contact {\n    display: flex;\n    justify-content: space-between;\n    margin-top: 15px;\n}\n.plate-price > span:nth-child(1) {\n    display: inline-block;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 13.9173px;\n    line-height: 21px;\n    width: 150px;\n    color: #898484;\n}\n.plate-price > span:nth-child(2) {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 15.041px;\n    line-height: 25px;\n    color: #000000;\n}\n.contact-check > div {\n    display: inline-block;\n    margin-right: 15px;\n}\n.contact-check > div > img:nth-child(1) {\n    margin-right: 10px;\n}\n.contact-check > button {\n    border: none;\n    outline: none;\n    background: #c4456f;\n    border-radius: 3.93246px;\n    padding: 9px 22px;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 11.346px;\n    line-height: 17px;\n    text-align: center;\n    color: #ffffff;\n}\n.venue-about {\n    margin: 15px 0;\n}\n.venue-about > h2 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 17.6485px;\n    line-height: 26px;\n    color: #000000;\n    margin-bottom: 7px;\n}\n.venue-about > p {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 16px;\n    line-height: 24px;\n    color: #000000;\n    margin: 0;\n}\n.venue-area-available {\n    margin-bottom: 15px;\n}\n.venue-area-avai